@charset "UTF-8";
/*프로모션 색상*/
/*widget_grid 배경색상*/
/*가변 마진 공식 (적용한 마진값 /겉박스)*100 */
/*가변 vw 공식 ((원하는 값)*100/페이지 width크기)*/
.fourth-section {
  position: relative;
  overflow: hidden;
  width: 100%;
  -webkit-transition: all 0.4s ease-out;
  -moz-transition: all 0.4s ease-out;
  -ms-transition: all 0.4s ease-out;
  -o-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
  font-size: 16px;
  padding-top: 11.98%;
  padding-bottom: 57.71%;
  max-height: 1590px; }
  .fourth-section--article {
    position: relative;
    width: 70%;
    margin: 0 auto;
    /*모바일 -> block*/ }
    .fourth-section--article > div {
      display: inline-block;
      -webkit-transition: all 0.2s ease-out;
      -moz-transition: all 0.2s ease-out;
      -ms-transition: all 0.2s ease-out;
      -o-transition: all 0.2s ease-out;
      transition: all 0.2s ease-out; }
    .fourth-section--article > div:nth-child(1) h1 {
      color: #3726af;
      font-size: 2.083vw;
      font-weight: bolder;
      word-break: keep-all;
      line-height: normal; }
      .fourth-section--article > div:nth-child(1) h1 em {
        font-weight: 300; }
    .fourth-section--article > div:nth-child(1) p {
      margin-top: 2.08vw;
      font-size: .75vw;
      font-weight: 100;
      line-height: 1.88;
      opacity: .7; }
  .fourth-section .fourth-section--mobile {
    display: none; }
    .fourth-section .fourth-section--mobile > div {
      margin: 0 auto;
      overflow: hidden;
      text-indent: -9999px; }
    .fourth-section .fourth-section--mobile .fourth-section--mobile--devices {
      margin-top: 60px;
      width: 83%;
      padding-bottom: 79.71%;
      background: url("./mobile-m-4-devices@2x.png") no-repeat center/contain; }
    .fourth-section .fourth-section--mobile .fourth-section--mobile--mobile {
      margin-top: 66px;
      width: 83%;
      padding-bottom: 66.1836%;
      background: url("./mobile-m-4-mobile@2x.png") no-repeat center/contain; }
    .fourth-section .fourth-section--mobile .fourth-section--mobile--pictures {
      width: 100%;
      padding-bottom: 100.9662%;
      background: url("./mobile-m-4-pics@2x.png") no-repeat center/contain; }

@media only screen and (min-width: 2550px) {
  .fourth-section {
    max-height: 3180px; } }

@media only screen and (max-width: 640px) {
  .fourth-section {
    background: url("./mobile-m-4-rectangle.svg") no-repeat center/cover;
    padding-top: 28.0193%;
    padding-bottom: 0; }
    .fourth-section br {
      display: none; }
  .fourth-section .fourth-section--mobile {
    display: block; }
  .fourth-section--article {
    width: 83%;
    text-align: center;
    margin: 0 auto; }
  .fourth-section--article > div:nth-child(1) h1 {
    font-size: 8.6956vw;
    width: 94%;
    margin: 0 auto; }
    .fourth-section--article > div:nth-child(1) h1 em {
      font-size: 4vw;
      text-align: left; }
  .fourth-section--article > div:nth-child(1) p {
    margin-top: 26px;
    line-height: 1.67;
    font-size: 4.3478vw;
    opacity: .75; } }
