.fifth-section {
  position: relative;
  width: 100%;
  font-size: 16px;
  padding-top: 19.9%;
  padding-bottom: 19.9%;
  -webkit-transition: all 0.4s ease-out;
  -moz-transition: all 0.4s ease-out;
  -ms-transition: all 0.4s ease-out;
  -o-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
  max-height: 983px; }
  .fifth-section svg {
    position: absolute;
    width: 29.4791%;
    height: 58.596%;
    bottom: 20.753%;
    left: 15%; }
  .fifth-section > div {
    position: relative;
    left: 16.9791%;
    width: 23%; }
    .fifth-section > div h1 {
      position: relative;
      font-size: 2.29vw;
      font-weight: bold;
      color: #fff;
      z-index: 1; }
    .fifth-section > div p {
      position: relative;
      margin-top: 2.08vw;
      z-index: 1;
      font-size: 1.2vw;
      font-weight: 100;
      line-height: 1.5;
      font-family: "Noto Sans";
      color: #fff; }
      .fifth-section > div p em {
        font-weight: bolder;
        font-size: 1.25vw;
        font-family: "Noto Sans CJK KR"; }
    .fifth-section > div em {
      font-weight: 500;
      vertical-align: baseline;
      color: #fff; }

@media only screen and (min-width: 2550px) {
