@charset "UTF-8";
.main-section {
  position: relative;
  width: 100%;
  height: 100vh;
  display: block;
  background: url("./web-m-1-bg@2x.jpg") no-repeat bottom center/cover;
  -webkit-transition: all 0.4s ease-out;
  -moz-transition: all 0.4s ease-out;
  -o-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out; }
  .main-section--content {
    position: absolute;
    top: 38.5554%;
    left: 17%;
    width: 100%; }
    .main-section--content--text {
      position: relative;
      z-index: 4;
      margin: 0 auto; }
      .main-section--content--text__upper h1 {
        font-size: 2.6vw;
        font-weight: bold;
        letter-spacing: 1px; }
      .main-section--content--text__upper p {
        margin-top: 1.3625vw;
        font-size: 1.3541vw;
        font-weight: 100;
        line-height: normal;
        letter-spacing: 1px; }
      .main-section--content--text__lower {
        margin-top: 3.0895vw; }
        .main-section--content--text__lower p {
          font-size: 1.2541vw;
          line-height: normal;
          font-weight: 100; }
      .main-section--content--text h1, .main-section--content--text p {
        z-index: 5;
        position: relative;
        color: #fff; }
    .main-section--content .outer {
      display: table;
      width: 100%;
      height: 100%; }
      .main-section--content .outer .inner {
        display: table-cell;
        vertical-align: middle;
        text-align: center; }
        .main-section--content .outer .inner .centered {
          position: relative;
          display: inline-block; }
  .main-section svg {
    position: absolute;
    width: 35%;
    height: auto;
    top: 15.1577%;
    left: 50%;
    min-width: 274px;
    min-height: 280px; }

@media only screen and (max-width: 1919px) {
  .main-section {
    padding-bottom: 51.65528%;
    height: initial; } }

@media only screen and (max-width: 640px) {
  .main-section {
    padding: 0;
    height: 696px;
    background: url("./mobile-m-1-bg.jpg") no-repeat center/cover; }
    .main-section svg {
      top: initial;
      -webkit-transform: translateX(-50%);
      -moz-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
      -o-transform: translateX(-50%);
      transform: translateX(-50%);
      bottom: 13.5057%;
      width: 66.1836%; }
  .main-section--content--text {
    display: block; }
  .main-section--content--text__lower {
    margin-top: 21px; }
    .main-section--content--text__lower p {
      font-size: 10px;
      line-height: 16px;
      letter-spacing: 0; }
  .main-section--content--text__upper h1 {
    font-size: 17px;
    margin-bottom: 9px; }
  .main-section--content--text__upper p {
    margin-top: 9px;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0; }
  .main-section--content {
    top: 60.63218%;
    left: 0;
    display: block; } }

/*글자 크기*/
@keyframes turnSvg {
  from {
    -webkit-transform: rotate(0);
    -moz-transform: rotate(0);
    -ms-transform: rotate(0);
    -o-transform: rotate(0);
    transform: rotate(0); }
  to {
    -webkit-transform: rotate(20deg);
    -moz-transform: rotate(20deg);
    -ms-transform: rotate(20deg);
    -o-transform: rotate(20deg);
    transform: rotate(20deg); } }
