.seventh-section {
  position: relative;
  width: 100%;
  padding-top: 19.1%;
  padding-bottom: 19.1%;
  -webkit-transition: all 0.4s ease-out;
  -moz-transition: all 0.4s ease-out;
  -ms-transition: all 0.4s ease-out;
  -o-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
  max-height: 983px; }
  .seventh-section .mobile-break {
    display: none; }
  .seventh-section svg {
    position: absolute;
    top: 20.7528%;
    left: 55.5208%;
    width: 29.4791%;
    height: 58.5961%; }
  .seventh-section--txt {
    position: relative;
    left: 58%;
    width: 24%; }
    .seventh-section--txt h1 {
      position: relative;
      font-size: 2.29vw;
      font-weight: bold;
      color: #fff;
      z-index: 1; }
    .seventh-section--txt p {
      position: relative;
      margin-top: 2.08vw;
      font-size: 1.2vw;
      font-weight: 300;
      line-height: 1.5;
      color: #fff;
      z-index: 1;
      word-break: break-all;
      font-family: "Noto Sans"; }
      .seventh-section--txt p em {
        font-weight: bolder;
        font-size: 1.25vw;
        font-family: "Noto Sans CJK KR";
        vertical-align: baseline; }

@media only screen and (min-width: 2550px) {
