.secondary-section {
  position: relative;
  width: 100%;
  background: #f6f4fc url("./web-m-2-wave@2x.png") no-repeat center/cover;
  -webkit-transition: 0.25s ease-out;
  -moz-transition: 0.25s ease-out;
  -ms-transition: 0.25s ease-out;
  -o-transition: 0.25s ease-out;
  transition: 0.25s ease-out;
  padding-top: 13.2558%;
  padding-bottom: 32.7906%;
  font-size: 16px; }
  .secondary-section--content {
    width: 70%;
    margin: 0 auto;
    text-align: center; }
    .secondary-section--content--head {
      display: inline-block;
      width: 19.5416%;
      padding: 10% 0;
      background: url("./splash.png") no-repeat center/cover; }
      .secondary-section--content--head .outer {
        display: table;
        width: 100%;
        height: 100%; }
        .secondary-section--content--head .outer .inner {
          display: table-cell;
          vertical-align: middle;
          text-align: center; }
          .secondary-section--content--head .outer .inner .centered {
            position: relative;
            display: inline-block; }
            .secondary-section--content--head .outer .inner .centered h1 {
              text-align: center;
              font-size: 2.291vw;
              color: #fff;
              font-weight: bold;
              display: inline-block; }
            .secondary-section--content--head .outer .inner .centered span {
              vertical-align: middle;
              display: inline-block;
              font-size: 2.291vw;
              color: #fff; }
    .secondary-section--content p {
      margin: 42px auto 0;
      font-size: .75vw;
      line-height: 1.88;
      font-weight: 300;
      opacity: .7; }
    .secondary-section--content em {
      vertical-align: baseline;
      font-weight: bold; }

@media only screen and (max-width: 640px) {
  .secondary-section {
    padding-top: 14.4927%;
    padding-bottom: 49.0338%;
    background: url("./mobile-m-2-wave.svg") no-repeat bottom/100% auto; }
  .secondary-section--content {
    width: 90%; }
  .secondary-section--content--head {
    width: 85%;
    padding-top: 37.6811%;
    padding-bottom: 48.6473%; }
  .secondary-section--content--head .outer .inner .centered h1 {
    font-size: 10.628vw; }
  .secondary-section--content p {
    margin: 10px auto 0;
    font-size: 4vw;
    width: 85%;
    line-height: 1.5em; } }
